<template>
	<div class="products-container" v-if="!showProductContainer">
		<a class="product-item"
			 v-for="product of searchProducts"
			 :key="product.id"
			 v-if="$route.meta.name === 'home' ||
						 $route.meta.name === 'exclusivos' && product.exclusivo ||
						 $route.meta.name === 'promocoes' && product.promocao ||
						 $route.meta.name === 'favoritos' && checked(product.id)">

			<div class="product-header" @click="updateShowProduct(product.id)">
				<img :src="product.imagem" class="image">
				<span v-if="product.exclusivo" class="exclusive">Exclusivo</span>
				<span v-if="product.promocao" class="promotion">Promoção</span>
			</div>

			<div class="product-content">
				<div class="group-data">
					<label class="price" @click="updateShowProduct(product.id)">{{ product.valor | formatPrice }}</label>
					<div class="favorite">
						<input type="checkbox"
									 class="checkbox"
									 :title="checked(product.id) ? 'Produto salvo como Favorito!' : 'Salvar produto como Favorito?'"
									 :value="product.id" :checked="checked(product.id)"
									 @input="setFavorites($event.target.value)"/>
						<font-awesome-icon :class="[ checked(product.id) ? 'icon-heart primary' : 'icon-heart' ]" :icon="['fas', 'heart']"/>
					</div>
				</div>
				<p class="name" @click="updateShowProduct(product.id)">{{ product.nome }}</p>
				<p class="short-description" @click="updateShowProduct(product.id)">{{ product.decricaoCurta }}</p>
			</div>
		</a>

		<div class="alert-message" v-if="noResults">
			<font-awesome-icon class="icon" :icon="['fas', 'exclamation-circle']"/>
			<h3 class="message">Sua busca não retornou nenhum resultado</h3>
			<span class="instruction">Tente novamente utilizando palavras diferentes.</span>
		</div>

		<div class="alert-message" v-if="$route.meta.name == 'favoritos' && counter === 0">
			<font-awesome-icon class="icon" :icon="['fas', 'heart-broken']"/>
			<h3 class="message">Você não possui nenhum produto favorito</h3>
			<span class="instruction">Para adicionar um produto aos seus favoritos, clique no icone do coração no produto desejado.</span>
		</div>
	</div>
</template>

<script>
	import { mapState } from 'vuex'

	export default {
		name: 'products-container',

		computed: {
			...mapState({
				products: state => state.products,
				search: state => state.search,
				favorites: state => state.favorites,
				counter: state => state.counter,
				showProductContainer: state => state.showProductContainer,
				selectedProduct: state => state.selectedProduct,
				noResults: state => state.noResults
			}),

			searchProducts: function () {
				let resultByName = this.search ? this.products.filter(product => {
					return product.nome.includes(this.search);
				}) : this.products;

				if (this.firstTime === false) {
					this.firstTime = true;
					return resultByName;
				}

				if (resultByName.length > 0) {
					this.$store.commit('setNoResult', false);
					return resultByName;
				}

				else {
					let resultByDescription = this.search ? this.products.filter(product => {
						return product.decricaoCurta.includes(this.search);
					}) : this.products;

					if (resultByDescription.length > 0) {
						this.$store.commit('setNoResult', false);
						return resultByDescription;
					}

					this.$store.commit('setNoResult', true);
					return resultByDescription;
				}
			}
		},

		filters: {
			formatPrice (value) {
				return "R$ " + value.toFixed(2).replace('.', ',').replace(/(\d)(?=(\d{3})+\,)/g, "$1.");
			}
		},

		methods: {
			updateShowProduct: function (product) {
				this.$store.commit('setSelectedProduct', product);
				return this.$store.commit('updateShowProduct');
			},

			setFavorites: function (product) {
				if (this.favorites[product]) {
					this.$store.commit('decrementCounter');
					return this.$store.commit('setFavorites', {
						product: product,
						value: false
					});
				}

				this.$store.commit('incrementCounter');
				return this.$store.commit('setFavorites', {
					product: product,
					value: true
				});
			},

			checked: function (product) {
				if (this.favorites[product]) {
					return true;
				}
				return false;
			}
		}
	}
</script>

<style lang="sass">
	@import '../assets/sass/base.sass'

	.products-container
		height: auto;
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		padding: 25px;

		.product-item
			background: map-get($colors, alternative);
			border-radius: map-get($globals, border-radius);
			box-shadow: 0px 10px 20px rgba(22, 49, 155, 0.16);
			display: flex;
			flex-flow: column;
			margin: 25px;
			max-width: 250px;
			text-decoration: none;

			.product-header
				position: relative;
				height: 165px;
				width: 250px;
				cursor: pointer;

				.image
					height: 100%;
					width: 100%;
					object-fit: cover;
					object-position: bottom;
					border-radius: map-get($globals, border-radius) map-get($globals, border-radius) 0 0;

				.exclusive
					position: absolute;
					display: flex;
					justify-content: center;
					align-items: center;
					background: transparent linear-gradient(125deg, map-get($colors, secondary-alt) 0%, map-get($colors, secondary) 100%) 0% 0% no-repeat padding-box;
					color: map-get($colors, alternative);
					border-radius: 10px 0 0 0;
					font-weight: 300;
					font-size: 12px;
					right: 0;
					top: 140px;
					height: 25px;
					width: 80px;

				.promotion
					position: absolute;
					display: flex;
					justify-content: center;
					align-items: center;
					background: transparent linear-gradient(125deg, map-get($colors, primary-alt) 0%, map-get($colors, primary) 100%) 0% 0% no-repeat padding-box;
					color: map-get($colors, alternative);
					border-radius: 10px 0 0 0;
					font-weight: 300;
					font-size: 12px;
					right: 0;
					top: 140px;
					height: 25px;
					width: 80px;

			.product-content
				// padding: 15px 20px;

				.group-data
					display: flex;
					flex-flow: row;
					justify-content: space-between;
					align-items: center;

					.price
						font-size: 19px;
						font-weight: 700;
						color: map-get($colors, primary);
						width: calc(100% - 64px);
						padding: 20px 0px 0px 20px;
						cursor: pointer;

					.favorite
						height: 45px;
						width: 45px;
						position: relative;
						display: flex;
						align-items: flex-end;
						justify-content: flex-start;

						input[type='checkbox'].checkbox
							height: 26px;
							width: 26px;
							z-index: 1;
							-webkit-appearance: none;
							cursor: pointer;

						.icon-heart
							position: absolute;
							height: 26px;
							width: 26px;
							color: map-get($colors, spacegray);

						.primary
							color: map-get($colors, primary);

				.name
					font-size: 16px;
					font-weight: 700;
					color: map-get($colors, default);
					padding: 12px 20px;
					cursor: pointer;

				.short-description
					font-size: 13px;
					color: map-get($colors, default-light);
					line-height: 1.5;
					padding: 0px 20px 20px 20px;
					cursor: pointer;

		.alert-message
			display: flex;
			flex-flow: column;
			justify-content: center;
			align-items: center;
			margin: 25px;
			max-width: 430px;
			color: map-get($colors, default-light);

			.icon
				height: 70px;
				width: 70px;
				color: map-get($colors, spacegray);

			.message
				font-size: 19px;
				margin-top: 10px;
				text-align: center;

			.instruction
				font-size: 13px;
				line-height: 1.5;
				margin-top: 5px;
				text-align: center;

	@media (max-width: 768px)
		.products-container
			padding: 15px;

			.product-item
				max-width: 100%;

				.product-header
					width: 100%;

</style>
