<template>
	<div class="header-container">
		<div class="header-content" v-if="!showProductContainer">
			<h2 class="title"><span class="bold">Empresa XPTO</span>{{ $route.meta.title }}</h2>
			<h3 class="subtitle">{{ $route.meta.subtitle }}</h3>
		</div>

		<div class="header-content"
				 v-for="product of products"
				 :key="product.id"
				 v-if="product.id === selectedProduct && showProductContainer">

			<div class="product-content">
				<div class="group-data">
					<p class="name">{{ product.nome }}</p>
					<div class="row-data">
						<label class="price">{{ product.valor | formatPrice }}</label>
						<div class="favorite">
							<input type="checkbox"
										 class="checkbox"
										 :title="checked(product.id) ? 'Produto salvo como Favorito!' : 'Salvar produto como Favorito?'"
										 :value="product.id" :checked="checked(product.id)"
										 @input="setFavorites($event.target.value)"/>
							<font-awesome-icon :class="[ checked(product.id) ? 'icon-heart primary' : 'icon-heart' ]" :icon="['fas', 'heart']"/>
						</div>
					</div>
				</div>
				<p class="short-description">{{ product.decricaoCurta }}</p>
			</div>
		</div>
		
		<div class="header-search">
			<a class="link-return" v-if="showProductContainer" @click="updateShowProduct">
				<img class="icon-return" src="../assets/icons/return/return-big.png" title="Retornar para tela anterior?">		
			</a>
			<div class="field-container">
				<img class="icon-loupe" src="../assets/icons/loupe/loupe-big.png">
				<input type="text" class="field-search" placeholder="Buscar produto" @input="search($event.target.value)">
			</div>
		</div>
	</div>
</template>

<script>
	import { mapState } from 'vuex'

	export default {
		name: 'header-container',

		computed: {
			...mapState({
				products: state => state.products,
				favorites: state => state.favorites,
				counter: state => state.counter,
				showProductContainer: state => state.showProductContainer,
				selectedProduct: state => state.selectedProduct
			}),
		},

		filters: {
			formatPrice (value) {
				return "R$ " + value.toFixed(2).replace('.', ',').replace(/(\d)(?=(\d{3})+\,)/g, "$1.");
			}
		},

		methods: {
			updateShowProduct: function () {
				this.$store.commit('setSelectedProduct', null);
				return this.$store.commit('updateShowProduct');
			},

			search: function(value) {
				// Reset page
				this.$store.commit('setSelectedProduct', null);
				this.$store.commit('hideProduct');

				// Converts the first letter of each word
				value = value.replace(/\w\S*/g, function(txt) {
					return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
				});

				this.$store.commit('setSearch', value);
			},

			setFavorites: function (product) {
				if (this.favorites[product]) {
					this.$store.commit('decrementCounter');
					return this.$store.commit('setFavorites', {
						product: product,
						value: false
					});
				}

				this.$store.commit('incrementCounter');
				return this.$store.commit('setFavorites', {
					product: product,
					value: true
				});
			},

			checked: function (product) {
				if (this.favorites[product]) {
					return true;
				}
				return false;
			}
		}
	}
</script>

<style lang="sass">
	@import '../assets/sass/base.sass'

	.header-container
		height: auto;
		width: 100%;
		display: flex;
		flex-flow: row;
		justify-content: flex-start;
		padding: 25px;

		.header-content
			height: auto;
			width: 100%;
			margin: 25px;
			display: flex;
			flex-flow: column;
			align-items: flex-start;
			justify-content: center;

			.title
				font-size: 19px;

				.bold
					font-weight: 700;

			.subtitle
				font-size: 13px;
				color: map-get($colors, default-light);
				line-height: 1.5;
				margin-top: 5px;

			.product-content
				.group-data
					display: flex;
					flex-flow: row;
					justify-content: flex-start;
					align-items: center;

					.name
						font-size: 19px;
						font-weight: 700;
						color: map-get($colors, default);
						margin-bottom: 12px;

					.row-data
						display: flex;

						.price
							font-size: 19px;
							font-weight: 700;
							color: map-get($colors, primary);
							margin: 0 20px 12px 20px;

						.favorite
							height: 26px;
							width: 26px;
							position: relative;
							display: flex;
							align-items: center;
							justify-content: center;
							margin-bottom: 12px;

							input[type='checkbox'].checkbox
								height: 100%;
								width: 100%;
								z-index: 1;
								-webkit-appearance: none;
								cursor: pointer;

							.icon-heart
								position: absolute;
								height: 100%;
								width: 100%;
								color: map-get($colors, spacegray);

							.primary
								color: map-get($colors, primary);

				.short-description
						font-size: 13px;
						color: map-get($colors, default-light);
						line-height: 1.5;

		.header-search
			height: auto;
			width: 50%;
			margin: 25px;
			display: flex;
			flex-flow: row;
			justify-content: center;
			align-items: center;

			.link-return
				height: 30px;
				width: 40px;
				cursor: pointer;
				margin-right: 10px;
				display: flex;
				justify-content: center;
				align-items: center;

				.icon-return
					width: 100%;
					padding-right: 10px;
					border-right: 1px solid map-get($colors, spacegray);

			.field-container
				position: relative;
				display: flex;
				width: -webkit-fill-available;
				max-width: 530px;

				.field-search
					height: 40px;
					width: inherit;
					text-align: start;
					font-family: map-get($globals, font-family);
					font-size: map-get($globals, font-size);
					color: map-get($colors, default);
					border: none;
					box-shadow: 0px 3px 6px #2959B729;
					border-radius: 20px;
					padding: 5px 5px 5px 50px;

				.icon-loupe
					position: absolute;
					left: 0;
					top: 0;
					height: 20px;
					margin: 10px 0px 10px 10px;
					padding-right: 10px;
					border-right: 1px solid map-get($colors, spacegray);

	@media (max-width: 992px)
		.header-container
			flex-flow: column;
			justify-content: center;
			padding: 15px;

			.header-content
				width: auto;
				align-items: center;

				.title
					text-align: center;

				.subtitle
					text-align: center;

				.product-content
					.group-data
						justify-content: center;
						flex-flow: column;

					.short-description
						text-align: center;

			.header-search			
				width: auto;
</style>
