import Vue from "vue";
import Vuex from "vuex";
import Axios from "axios";

Vue.use(Vuex, Axios);

export default new Vuex.Store({
  state: {
    products: [],
    search: "",
    noResults: false,
    showProductContainer: false,
    selectedProduct: "",
    favorites: {},
    counter: 0
  },
  mutations: {
    SET_PRODUCTS(state, payload) {
      state.products = payload;
    },

    setSearch(state, payload) {
      state.search = payload;
    },

    setNoResult(state, payload) {
      state.noResults = payload;
    },

    updateShowProduct(state) {
      state.showProductContainer = !state.showProductContainer;
    },

    hideProduct(state) {
      state.showProductContainer = false;
    },

    setSelectedProduct(state, payload) {
      state.selectedProduct = payload;
    },

    setFavorites(state, { product, value }) {
      return new Promise(resolve => {
        Vue.set(state.favorites, product, value);
        resolve();
      });
    },

    incrementCounter(state) {
      state.counter++;
    },

    decrementCounter(state) {
      state.counter--;
    }
  },
  actions: {
    loadProducts({ commit }) {
      Axios.get("https://www.mocky.io/v2/5d3b57023000005500a2a0a6")
        .then(response => {
          let products = response.data.produtos;
          commit("SET_PRODUCTS", products);
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  modules: {}
});
