import Vue from "vue";
import VueRouter from "vue-router";
import Application from "../views/Application.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: "/home"
  },
  {
    path: "/home",
    component: Application,
    meta: {
      name: "home",
      title: " - Conheça todos os nossos produtos",
      subtitle:
        "Listagem de produtos marcados como favoritos - clique no produto desejado para saber mais"
    }
  },
  {
    path: "/exclusivos",
    component: Application,
    meta: {
      name: "exclusivos",
      title: " - Conheça nossos produtos exclusivos",
      subtitle:
        "Listagem de produtos exclusivos - clique no produto desejado para saber mais"
    }
  },
  {
    path: "/promocoes",
    component: Application,
    meta: {
      name: "promocoes",
      title: " - Conheça nossas promoções",
      subtitle:
        "Listagem de produtos em promoção - clique no produto desejado para saber mais"
    }
  },
  {
    path: "/favoritos",
    component: Application,
    meta: {
      name: "favoritos",
      title: " - Meus favoritos",
      subtitle:
        "Listagem de produtos marcados como favoritos - clique no produto desejado para saber mais"
    }
  }
];

const router = new VueRouter({
  routes
});

export default router;
