<template>
	<div class="product-container" v-if="showProductContainer">
		<div class="product-infos"
				 v-for="product of products"
				 :key="product.id"
				 v-if="product.id === selectedProduct">
			
			<div class="product-content">
				<div class="product-image">
					<img :src="product.imagem" class="image">
					<span v-if="product.exclusivo" class="exclusive">Exclusivo</span>
					<span v-if="product.promocao" class="promotion">Promoção</span>
				</div>
				<div class="product-details">
					<h3 class="title">Detalhes do Produto</h3>
					<p class="long-description">{{ product.descricaoLonga }}</p>
				</div>
			</div>

			<hr class="hr"/>

			<div class="datasheet">
				<h3 class="title">Ficha Técnica</h3>
				<ul class="datasheet-list" v-for="{titulo, descricao} in product.fichaTecnica" :key="titulo">
					<li class="description">
						<span class="bold">{{titulo}}:</span>&nbsp;{{descricao}}
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

